import { useState } from 'react';
import { GitBranch, Users, ArrowRightLeft } from 'lucide-react';
import { useConfigStore } from '@/stores/configStore';
import Button from '../ui/Button';
import Select from '../ui/Select';
import Textarea from '../ui/Textarea';
import Card from '../ui/Card';
import Badge from '../ui/Badge';
import { clsx } from 'clsx';

type OrchestrationPattern = 'supervisor' | 'swarm' | 'none';

export default function OrchestrationSection() {
  const { config, updateApp } = useConfigStore();
  const [pattern, setPattern] = useState<OrchestrationPattern>(
    config.app?.orchestration?.supervisor ? 'supervisor' :
    config.app?.orchestration?.swarm ? 'swarm' : 'none'
  );
  const [supervisorPrompt, setSupervisorPrompt] = useState('');

  const llms = config.resources?.llms || {};
  const agents = config.agents || {};

  const llmOptions = Object.entries(llms).map(([key, llm]) => ({
    value: key,
    label: `${key} (${llm.name})`,
  }));

  const agentOptions = Object.entries(agents).map(([key, agent]) => ({
    value: key,
    label: agent.name,
  }));

  const [selectedLLM, setSelectedLLM] = useState('');
  const [defaultAgent, setDefaultAgent] = useState('');

  const handleSave = () => {
    if (pattern === 'none') {
      updateApp({ orchestration: undefined });
      return;
    }

    if (!selectedLLM || !llms[selectedLLM]) return;

    if (pattern === 'supervisor') {
      updateApp({
        orchestration: {
          supervisor: {
            model: llms[selectedLLM],
            ...(supervisorPrompt && { prompt: supervisorPrompt }),
          },
        },
      });
    } else if (pattern === 'swarm') {
      updateApp({
        orchestration: {
          swarm: {
            model: llms[selectedLLM],
            ...(defaultAgent && agents[defaultAgent] && { default_agent: agents[defaultAgent] }),
          },
        },
      });
    }
  };

  return (
    <div className="space-y-6">
      {/* Header */}
      <div>
        <h2 className="text-2xl font-bold text-white">Orchestration</h2>
        <p className="text-slate-400 mt-1">
          Configure how agents work together in your multi-agent system
        </p>
      </div>

      {/* Pattern Selection */}
      <div className="grid grid-cols-3 gap-4">
        <Card
          variant={pattern === 'none' ? 'highlight' : 'interactive'}
          onClick={() => setPattern('none')}
          className={clsx(
            'cursor-pointer text-center py-6',
            pattern === 'none' && 'ring-2 ring-blue-500'
          )}
        >
          <div className="w-12 h-12 mx-auto rounded-lg bg-slate-700 flex items-center justify-center mb-3">
            <GitBranch className="w-6 h-6 text-slate-400" />
          </div>
          <h3 className="font-medium text-white">No Orchestration</h3>
          <p className="text-xs text-slate-500 mt-1">Single agent mode</p>
        </Card>

        <Card
          variant={pattern === 'supervisor' ? 'highlight' : 'interactive'}
          onClick={() => setPattern('supervisor')}
          className={clsx(
            'cursor-pointer text-center py-6',
            pattern === 'supervisor' && 'ring-2 ring-blue-500'
          )}
        >
          <div className="w-12 h-12 mx-auto rounded-lg bg-blue-500/20 flex items-center justify-center mb-3">
            <Users className="w-6 h-6 text-blue-400" />
          </div>
          <h3 className="font-medium text-white">Supervisor</h3>
          <p className="text-xs text-slate-500 mt-1">Central routing agent</p>
        </Card>

        <Card
          variant={pattern === 'swarm' ? 'highlight' : 'interactive'}
          onClick={() => setPattern('swarm')}
          className={clsx(
            'cursor-pointer text-center py-6',
            pattern === 'swarm' && 'ring-2 ring-blue-500'
          )}
        >
          <div className="w-12 h-12 mx-auto rounded-lg bg-purple-500/20 flex items-center justify-center mb-3">
            <ArrowRightLeft className="w-6 h-6 text-purple-400" />
          </div>
          <h3 className="font-medium text-white">Swarm</h3>
          <p className="text-xs text-slate-500 mt-1">Peer-to-peer handoffs</p>
        </Card>
      </div>

      {/* Pattern Configuration */}
      {pattern !== 'none' && (
        <Card className="space-y-4">
          <h3 className="font-medium text-white">
            {pattern === 'supervisor' ? 'Supervisor Configuration' : 'Swarm Configuration'}
          </h3>

          {llmOptions.length === 0 && (
            <div className="bg-amber-500/10 border border-amber-500/30 rounded-lg p-4 text-amber-400 text-sm">
              You need to add an LLM first to configure orchestration.
            </div>
          )}

          <Select
            label="Orchestration Model"
            options={llmOptions}
            value={selectedLLM}
            onChange={(e) => setSelectedLLM(e.target.value)}
            placeholder="Select an LLM..."
            hint={pattern === 'supervisor' 
              ? 'The LLM that will route requests to appropriate agents'
              : 'The LLM used for agent handoff decisions'
            }
          />

          {pattern === 'supervisor' && (
            <Textarea
              label="Supervisor Prompt (Optional)"
              placeholder="Custom instructions for the supervisor agent..."
              value={supervisorPrompt}
              onChange={(e) => setSupervisorPrompt(e.target.value)}
              rows={4}
              hint="Override the default supervisor prompt"
            />
          )}

          {pattern === 'swarm' && agentOptions.length > 0 && (
            <Select
              label="Default Agent"
              options={agentOptions}
              value={defaultAgent}
              onChange={(e) => setDefaultAgent(e.target.value)}
              placeholder="Select default agent..."
              hint="The agent that handles initial requests"
            />
          )}

          <div className="flex justify-end pt-4">
            <Button onClick={handleSave} disabled={!selectedLLM}>
              Save Configuration
            </Button>
          </div>
        </Card>
      )}

      {/* Pattern Explanation */}
      <Card className="bg-slate-800/30">
        <h4 className="font-medium text-slate-300 mb-3">About Orchestration Patterns</h4>
        <div className="space-y-3 text-sm text-slate-400">
          <div>
            <Badge variant="info" className="mb-1">Supervisor</Badge>
            <p>A central supervisor agent analyzes each request and routes it to the most appropriate specialized agent. Best for systems with clearly defined agent responsibilities.</p>
          </div>
          <div>
            <Badge variant="info" className="mb-1">Swarm</Badge>
            <p>Agents can hand off conversations directly to each other based on handoff prompts. More flexible and allows for dynamic agent collaboration.</p>
          </div>
        </div>
      </Card>
    </div>
  );
}

