/**
 * Native Databricks API service - calls Databricks REST APIs directly from the browser.
 * 
 * Authentication resolution:
 * 1. X-Forwarded-Access-Token header (Databricks App with user auth)
 * 2. DATABRICKS_TOKEN environment variable (via backend)
 * 3. Manual configuration (user enters in UI)
 * 
 * Host resolution:
 * 1. window.location.origin (when running in Databricks App)
 * 2. DATABRICKS_HOST environment variable (via backend, for local dev)
 * 3. Manual configuration (user enters in UI)
 * 
 * Reference: https://apps-cookbook.dev/docs/streamlit/authentication/users_obo
 */

export interface DatabricksConfig {
  host: string;
  token: string;
  isAutoDetected?: boolean;
  source?: 'header' | 'env' | 'manual';
}

// Store configuration in memory
let config: DatabricksConfig | null = null;

const CONFIG_STORAGE_KEY = 'databricks_config';

/**
 * Check if we're running inside a Databricks App environment.
 */
export function isRunningInDatabricksApp(): boolean {
  const hostname = window.location.hostname;
  return (
    hostname.includes('.databricks') ||
    hostname.includes('.azuredatabricks.') ||
    hostname.endsWith('.databricks.com') ||
    hostname.endsWith('.gcp.databricks.com') ||
    hostname.endsWith('.cloud.databricks.com')
  );
}

/**
 * Get the Databricks workspace host from the current URL.
 */
export function getWorkspaceHost(): string {
  return window.location.origin;
}

/**
 * Initialize the Databricks configuration.
 */
export function setDatabricksConfig(newConfig: DatabricksConfig): void {
  config = newConfig;
  // Persist to localStorage (only for manual config)
  if (!newConfig.isAutoDetected) {
    try {
      localStorage.setItem(CONFIG_STORAGE_KEY, JSON.stringify(newConfig));
    } catch {
      // localStorage might not be available
    }
  }
}

/**
 * Get the current Databricks configuration.
 */
export function getDatabricksConfig(): DatabricksConfig | null {
  if (config) return config;
  
  // Try to load from localStorage
  try {
    const stored = localStorage.getItem(CONFIG_STORAGE_KEY);
    if (stored) {
      config = JSON.parse(stored);
      return config;
    }
  } catch {
    // localStorage might not be available
  }
  
  return null;
}

/**
 * Clear the Databricks configuration.
 */
export function clearDatabricksConfig(): void {
  config = null;
  try {
    localStorage.removeItem(CONFIG_STORAGE_KEY);
  } catch {
    // localStorage might not be available
  }
}

/**
 * Check if Databricks is configured.
 */
export function isDatabricksConfigured(): boolean {
  return getDatabricksConfig() !== null;
}

/**
 * Response from /api/auth/token endpoint.
 */
interface AuthTokenResponse {
  token: string | null;
  host: string | null;
  email?: string;
  user?: string;
  source?: 'header' | 'env' | 'manual' | null;
}

/**
 * Try to auto-detect Databricks configuration.
 * 
 * Resolution order:
 * 1. X-Forwarded-Access-Token header (Databricks App with user auth)
 * 2. DATABRICKS_TOKEN/DATABRICKS_HOST env vars (via backend)
 * 3. window.location.origin for host (when in Databricks App)
 * 
 * Reference: https://apps-cookbook.dev/docs/streamlit/authentication/users_obo
 */
export async function tryAutoDetectConfig(): Promise<{
  success: boolean;
  host: string | null;
  hasToken: boolean;
  inDatabricksApp: boolean;
  source?: 'header' | 'env' | 'manual' | null;
  error?: string;
}> {
  // If already configured, return current state
  if (isDatabricksConfigured()) {
    const cfg = getDatabricksConfig()!;
    return { 
      success: true, 
      host: cfg.host, 
      hasToken: true,
      inDatabricksApp: isRunningInDatabricksApp(),
      source: cfg.source,
    };
  }

  const inDatabricksApp = isRunningInDatabricksApp();

  // Try to get token and host from backend
  try {
    const response = await fetch('/api/auth/token', {
      credentials: 'include',
    });
    
    if (response.ok) {
      const data: AuthTokenResponse = await response.json();
      
      // Determine host: use backend's host or window.location.origin for Databricks Apps
      const host = data.host || (inDatabricksApp ? getWorkspaceHost() : null);
      
      if (host && data.token) {
        // Full auto-config: we have both host and token
        setDatabricksConfig({
          host,
          token: data.token,
          isAutoDetected: true,
          source: data.source || undefined,
        });
        return { 
          success: true, 
          host, 
          hasToken: true,
          inDatabricksApp,
          source: data.source,
        };
      }
      
      // Partial: we have host but no token
      if (host) {
        return { 
          success: false, 
          host, 
          hasToken: false,
          inDatabricksApp,
          source: data.source,
          error: 'Token not available. Set DATABRICKS_TOKEN env var or enable on-behalf-of-user auth.',
        };
      }
    }
  } catch {
    // Backend not available
  }

  // Fallback for Databricks App without backend token
  if (inDatabricksApp) {
    return { 
      success: false, 
      host: getWorkspaceHost(), 
      hasToken: false,
      inDatabricksApp: true,
      error: 'Token not available. Enable on-behalf-of-user auth or set DATABRICKS_TOKEN.',
    };
  }

  // Not in Databricks App and no env vars
  return { 
    success: false, 
    host: null, 
    hasToken: false,
    inDatabricksApp: false,
    error: 'Configure Databricks credentials manually.',
  };
}

class DatabricksApiError extends Error {
  constructor(
    message: string,
    public status?: number,
    public errorCode?: string
  ) {
    super(message);
    this.name = 'DatabricksApiError';
  }
}

/**
 * Make an authenticated request to the Databricks API via backend proxy.
 * This avoids CORS issues by routing all requests through our Flask backend.
 */
async function fetchDatabricks<T>(
  endpoint: string,
  options: RequestInit = {}
): Promise<T> {
  const cfg = getDatabricksConfig();
  if (!cfg) {
    throw new DatabricksApiError('Databricks not configured. Please set host and token.');
  }

  // Use the backend proxy to avoid CORS issues
  // The proxy endpoint is: /api/databricks/api/2.1/...
  const proxyUrl = `/api/databricks/api/2.1${endpoint}`;
  
  const response = await fetch(proxyUrl, {
    ...options,
    headers: {
      'Authorization': `Bearer ${cfg.token}`,
      'Content-Type': 'application/json',
      'X-Databricks-Host': cfg.host,
      ...options.headers,
    },
  });

  if (!response.ok) {
    let errorMessage = `API request failed: ${response.statusText}`;
    let errorCode: string | undefined;
    
    try {
      const errorBody = await response.json();
      errorMessage = errorBody.message || errorBody.error || errorMessage;
      errorCode = errorBody.error_code;
    } catch {
      // Couldn't parse error body
    }
    
    throw new DatabricksApiError(errorMessage, response.status, errorCode);
  }

  return response.json();
}

// ============================================================================
// Unity Catalog APIs
// ============================================================================

export interface Catalog {
  name: string;
  comment?: string;
  owner?: string;
}

export interface Schema {
  name: string;
  catalog_name: string;
  full_name: string;
  comment?: string;
  owner?: string;
}

export interface Table {
  name: string;
  catalog_name: string;
  schema_name: string;
  full_name: string;
  table_type?: string;
  comment?: string;
}

export interface UCFunction {
  name: string;
  catalog_name: string;
  schema_name: string;
  full_name: string;
  comment?: string;
  input_params?: { parameters?: Array<{ name: string; type_name?: string }> };
}

export interface Volume {
  name: string;
  catalog_name: string;
  schema_name: string;
  full_name: string;
  volume_type?: string;
  comment?: string;
}

export interface RegisteredModel {
  name: string;
  full_name: string;
  catalog_name?: string;
  schema_name?: string;
  comment?: string;
}

// ============================================================================
// Serving & Compute APIs
// ============================================================================

export interface ServingEndpoint {
  name: string;
  state?: { ready?: string; config_update?: string };
  creator?: string;
}

export interface SQLWarehouse {
  id: string;
  name: string;
  state?: string;
  cluster_size?: string;
  num_clusters?: number;
}

// ============================================================================
// Genie & Vector Search APIs
// ============================================================================

export interface GenieSpace {
  space_id: string;
  title: string;
  description?: string;
}

export interface VectorSearchEndpoint {
  name: string;
  endpoint_type?: string;
  endpoint_status?: { state?: string };
}

export interface VectorSearchIndex {
  name: string;
  endpoint_name: string;
  index_type?: string;
  primary_key?: string;
}

// ============================================================================
// Connection Status
// ============================================================================

export interface ConnectionStatus {
  connected: boolean;
  user?: string;
  display_name?: string;
  host?: string;
  error?: string;
  isAutoDetected?: boolean;
  source?: 'header' | 'env' | 'manual';
}

// ============================================================================
// API Methods
// ============================================================================

export const databricksNativeApi = {
  /**
   * Get connection status by fetching current user.
   */
  async getConnectionStatus(): Promise<ConnectionStatus> {
    const cfg = getDatabricksConfig();
    if (!cfg) {
      return { connected: false, error: 'Not configured' };
    }

    try {
      const response = await fetchDatabricks<{
        userName?: string;
        displayName?: string;
      }>('/preview/scim/v2/Me');
      
      return {
        connected: true,
        user: response.userName,
        display_name: response.displayName,
        host: cfg.host,
        isAutoDetected: cfg.isAutoDetected,
        source: cfg.source,
      };
    } catch (err) {
      return {
        connected: false,
        error: err instanceof Error ? err.message : 'Unknown error',
      };
    }
  },

  /**
   * List all accessible catalogs.
   */
  async listCatalogs(): Promise<Catalog[]> {
    const response = await fetchDatabricks<{ catalogs?: Catalog[] }>(
      '/unity-catalog/catalogs'
    );
    return response.catalogs || [];
  },

  /**
   * List all schemas in a catalog.
   */
  async listSchemas(catalogName: string): Promise<Schema[]> {
    const response = await fetchDatabricks<{ schemas?: Schema[] }>(
      `/unity-catalog/schemas?catalog_name=${encodeURIComponent(catalogName)}`
    );
    return response.schemas || [];
  },

  /**
   * List all tables in a schema.
   */
  async listTables(catalogName: string, schemaName: string): Promise<Table[]> {
    const response = await fetchDatabricks<{ tables?: Table[] }>(
      `/unity-catalog/tables?catalog_name=${encodeURIComponent(catalogName)}&schema_name=${encodeURIComponent(schemaName)}`
    );
    return response.tables || [];
  },

  /**
   * List all functions in a schema.
   */
  async listFunctions(catalogName: string, schemaName: string): Promise<UCFunction[]> {
    const response = await fetchDatabricks<{ functions?: UCFunction[] }>(
      `/unity-catalog/functions?catalog_name=${encodeURIComponent(catalogName)}&schema_name=${encodeURIComponent(schemaName)}`
    );
    return response.functions || [];
  },

  /**
   * List all volumes in a schema.
   */
  async listVolumes(catalogName: string, schemaName: string): Promise<Volume[]> {
    const response = await fetchDatabricks<{ volumes?: Volume[] }>(
      `/unity-catalog/volumes?catalog_name=${encodeURIComponent(catalogName)}&schema_name=${encodeURIComponent(schemaName)}`
    );
    return response.volumes || [];
  },

  /**
   * List all model serving endpoints.
   */
  async listServingEndpoints(): Promise<ServingEndpoint[]> {
    const response = await fetchDatabricks<{ endpoints?: ServingEndpoint[] }>(
      '/serving-endpoints'
    );
    return response.endpoints || [];
  },

  /**
   * List all SQL warehouses.
   */
  async listSQLWarehouses(): Promise<SQLWarehouse[]> {
    const response = await fetchDatabricks<{ warehouses?: SQLWarehouse[] }>(
      '/sql/warehouses'
    );
    return response.warehouses || [];
  },

  /**
   * List all Genie spaces.
   */
  async listGenieSpaces(): Promise<GenieSpace[]> {
    try {
      const response = await fetchDatabricks<{ spaces?: GenieSpace[] }>(
        '/genie/spaces'
      );
      return response.spaces || [];
    } catch {
      return [];
    }
  },

  /**
   * List all vector search endpoints.
   */
  async listVectorSearchEndpoints(): Promise<VectorSearchEndpoint[]> {
    try {
      const response = await fetchDatabricks<{ endpoints?: VectorSearchEndpoint[] }>(
        '/vector-search/endpoints'
      );
      return response.endpoints || [];
    } catch {
      return [];
    }
  },

  /**
   * List all vector search indexes for an endpoint.
   */
  async listVectorSearchIndexes(endpointName: string): Promise<VectorSearchIndex[]> {
    try {
      const response = await fetchDatabricks<{ vector_indexes?: VectorSearchIndex[] }>(
        `/vector-search/indexes?endpoint_name=${encodeURIComponent(endpointName)}`
      );
      return response.vector_indexes || [];
    } catch {
      return [];
    }
  },

  /**
   * List all registered models.
   */
  async listRegisteredModels(): Promise<RegisteredModel[]> {
    const response = await fetchDatabricks<{ registered_models?: RegisteredModel[] }>(
      '/unity-catalog/models'
    );
    return response.registered_models || [];
  },
};

export default databricksNativeApi;
