import { useState } from 'react';
import { Plus, Trash2, Cpu, RefreshCw } from 'lucide-react';
import { useConfigStore } from '@/stores/configStore';
import Button from '../ui/Button';
import Input from '../ui/Input';
import Select from '../ui/Select';
import Card from '../ui/Card';
import Modal from '../ui/Modal';
import Badge from '../ui/Badge';
import { useServingEndpoints } from '@/hooks/useDatabricks';

const COMMON_MODELS = [
  { value: 'databricks-claude-3-7-sonnet', label: 'Claude 3.7 Sonnet' },
  { value: 'databricks-claude-sonnet-4', label: 'Claude Sonnet 4' },
  { value: 'databricks-meta-llama-3-3-70b-instruct', label: 'Llama 3.3 70B Instruct' },
  { value: 'databricks-meta-llama-3-1-405b-instruct', label: 'Llama 3.1 405B Instruct' },
  { value: 'databricks-dbrx-instruct', label: 'DBRX Instruct' },
  { value: 'databricks-gte-large-en', label: 'GTE Large (Embeddings)' },
];

type ModelSource = 'preset' | 'endpoint' | 'custom';

export default function LLMsSection() {
  const { config, addLLM, removeLLM } = useConfigStore();
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [modelSource, setModelSource] = useState<ModelSource>('preset');
  const [formData, setFormData] = useState({
    name: '',
    modelName: '',
    customModelName: '',
    temperature: '0.1',
    maxTokens: '8192',
  });

  const { data: endpoints, loading: endpointsLoading, refetch: refetchEndpoints } = useServingEndpoints();

  const llms = config.resources?.llms || {};

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    let modelName = '';
    
    switch (modelSource) {
      case 'preset':
        modelName = formData.modelName;
        break;
      case 'endpoint':
        modelName = formData.modelName;
        break;
      case 'custom':
        modelName = formData.customModelName;
        break;
    }
    
    if (formData.name && modelName) {
      addLLM(formData.name, {
        name: modelName,
        temperature: parseFloat(formData.temperature),
        max_tokens: parseInt(formData.maxTokens),
      });
      setFormData({
        name: '',
        modelName: '',
        customModelName: '',
        temperature: '0.1',
        maxTokens: '8192',
      });
      setModelSource('preset');
      setIsModalOpen(false);
    }
  };

  const endpointOptions = [
    { value: '', label: 'Select an endpoint...' },
    ...(endpoints || []).map((e) => ({
      value: e.name,
      label: `${e.name}${e.state ? ` (${e.state})` : ''}`,
    })),
  ];

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div>
          <h2 className="text-2xl font-bold text-white">Language Models</h2>
          <p className="text-slate-400 mt-1">
            Configure LLMs that power your agents
          </p>
        </div>
        <Button onClick={() => setIsModalOpen(true)}>
          <Plus className="w-4 h-4" />
          Add LLM
        </Button>
      </div>

      {/* LLM List */}
      {Object.keys(llms).length === 0 ? (
        <Card className="text-center py-12">
          <Cpu className="w-12 h-12 mx-auto text-slate-600 mb-4" />
          <h3 className="text-lg font-medium text-slate-300 mb-2">No LLMs configured</h3>
          <p className="text-slate-500 mb-4">
            Add language models that will power your AI agents.
          </p>
          <Button onClick={() => setIsModalOpen(true)}>
            <Plus className="w-4 h-4" />
            Add Your First LLM
          </Button>
        </Card>
      ) : (
        <div className="grid gap-4">
          {Object.entries(llms).map(([key, llm]) => (
            <Card key={key} variant="interactive" className="group">
              <div className="flex items-center justify-between">
                <div className="flex items-center space-x-4">
                  <div className="w-10 h-10 rounded-lg bg-purple-500/20 flex items-center justify-center">
                    <Cpu className="w-5 h-5 text-purple-400" />
                  </div>
                  <div>
                    <h3 className="font-medium text-white">{key}</h3>
                    <p className="text-sm text-slate-400">{llm.name}</p>
                  </div>
                </div>
                <div className="flex items-center space-x-3">
                  <Badge variant="info">temp: {llm.temperature ?? 0.1}</Badge>
                  <Badge variant="default">tokens: {llm.max_tokens ?? 8192}</Badge>
                  <Button
                    variant="danger"
                    size="sm"
                    onClick={() => removeLLM(key)}
                    className="opacity-0 group-hover:opacity-100 transition-opacity"
                  >
                    <Trash2 className="w-4 h-4" />
                  </Button>
                </div>
              </div>
            </Card>
          ))}
        </div>
      )}

      {/* Add LLM Modal */}
      <Modal
        isOpen={isModalOpen}
        onClose={() => setIsModalOpen(false)}
        title="Add Language Model"
        description="Configure an LLM for your agents to use"
      >
        <form onSubmit={handleSubmit} className="space-y-4">
          <Input
            label="Reference Name"
            placeholder="e.g., default_llm"
            value={formData.name}
            onChange={(e) => setFormData({ ...formData, name: e.target.value })}
            hint="A unique identifier for this LLM in your config"
            required
          />

          {/* Model Source Selector */}
          <div className="space-y-2">
            <label className="block text-sm font-medium text-slate-300">Model Source</label>
            <div className="flex space-x-2">
              <button
                type="button"
                onClick={() => setModelSource('preset')}
                className={`flex-1 px-3 py-2 rounded-lg text-sm font-medium transition-colors ${
                  modelSource === 'preset'
                    ? 'bg-blue-500/20 text-blue-400 border border-blue-500/30'
                    : 'bg-slate-800 text-slate-400 border border-slate-700 hover:border-slate-600'
                }`}
              >
                Preset Models
              </button>
              <button
                type="button"
                onClick={() => setModelSource('endpoint')}
                className={`flex-1 px-3 py-2 rounded-lg text-sm font-medium transition-colors ${
                  modelSource === 'endpoint'
                    ? 'bg-blue-500/20 text-blue-400 border border-blue-500/30'
                    : 'bg-slate-800 text-slate-400 border border-slate-700 hover:border-slate-600'
                }`}
              >
                Serving Endpoint
              </button>
              <button
                type="button"
                onClick={() => setModelSource('custom')}
                className={`flex-1 px-3 py-2 rounded-lg text-sm font-medium transition-colors ${
                  modelSource === 'custom'
                    ? 'bg-blue-500/20 text-blue-400 border border-blue-500/30'
                    : 'bg-slate-800 text-slate-400 border border-slate-700 hover:border-slate-600'
                }`}
              >
                Custom
              </button>
            </div>
          </div>

          {modelSource === 'preset' && (
            <Select
              label="Model"
              options={[{ value: '', label: 'Select a model...' }, ...COMMON_MODELS]}
              value={formData.modelName}
              onChange={(e) => setFormData({ ...formData, modelName: e.target.value })}
              required
            />
          )}

          {modelSource === 'endpoint' && (
            <div className="space-y-2">
              <div className="flex items-center justify-between">
                <label className="block text-sm font-medium text-slate-300">Serving Endpoint</label>
                <button
                  type="button"
                  onClick={() => refetchEndpoints()}
                  className="text-xs text-slate-400 hover:text-white flex items-center space-x-1"
                  disabled={endpointsLoading}
                >
                  <RefreshCw className={`w-3 h-3 ${endpointsLoading ? 'animate-spin' : ''}`} />
                  <span>Refresh</span>
                </button>
              </div>
              <Select
                options={endpointOptions}
                value={formData.modelName}
                onChange={(e) => setFormData({ ...formData, modelName: e.target.value })}
                disabled={endpointsLoading}
                required
              />
              {endpointsLoading && (
                <p className="text-xs text-slate-500">Loading endpoints from Databricks...</p>
              )}
            </div>
          )}

          {modelSource === 'custom' && (
            <Input
              label="Custom Model Name"
              placeholder="e.g., my-custom-endpoint"
              value={formData.customModelName}
              onChange={(e) => setFormData({ ...formData, customModelName: e.target.value })}
              hint="Enter the name of your custom model endpoint"
              required
            />
          )}

          <div className="grid grid-cols-2 gap-4">
            <Input
              label="Temperature"
              type="number"
              step="0.1"
              min="0"
              max="2"
              value={formData.temperature}
              onChange={(e) => setFormData({ ...formData, temperature: e.target.value })}
              hint="0.0 = deterministic, 2.0 = creative"
            />
            <Input
              label="Max Tokens"
              type="number"
              min="1"
              max="128000"
              value={formData.maxTokens}
              onChange={(e) => setFormData({ ...formData, maxTokens: e.target.value })}
              hint="Maximum response length"
            />
          </div>
          <div className="flex justify-end space-x-3 pt-4">
            <Button variant="secondary" type="button" onClick={() => setIsModalOpen(false)}>
              Cancel
            </Button>
            <Button type="submit">
              Add LLM
            </Button>
          </div>
        </form>
      </Modal>
    </div>
  );
}
