import { create } from 'zustand';
import { AppConfig, AgentModel, ToolModel, LLMModel, SchemaModel, GuardrailModel, PromptModel } from '@/types/dao-ai-types';

interface ConfigState {
  config: AppConfig;
  setConfig: (config: AppConfig) => void;
  updateConfig: (updates: Partial<AppConfig>) => void;
  
  // Schemas
  addSchema: (name: string, schema: SchemaModel) => void;
  updateSchema: (name: string, updates: Partial<SchemaModel>) => void;
  removeSchema: (name: string) => void;
  
  // Resources
  addLLM: (name: string, llm: LLMModel) => void;
  updateLLM: (name: string, updates: Partial<LLMModel>) => void;
  removeLLM: (name: string) => void;
  
  // Tools
  addTool: (tool: ToolModel) => void;
  updateTool: (name: string, updates: Partial<ToolModel>) => void;
  removeTool: (name: string) => void;
  
  // Guardrails
  addGuardrail: (guardrail: GuardrailModel) => void;
  updateGuardrail: (name: string, updates: Partial<GuardrailModel>) => void;
  removeGuardrail: (name: string) => void;
  
  // Prompts
  addPrompt: (prompt: PromptModel) => void;
  updatePrompt: (name: string, updates: Partial<PromptModel>) => void;
  removePrompt: (name: string) => void;
  
  // Agents
  addAgent: (agent: AgentModel) => void;
  updateAgent: (name: string, updates: Partial<AgentModel>) => void;
  removeAgent: (name: string) => void;
  
  // App config
  updateApp: (updates: Partial<AppConfig['app']>) => void;
  
  reset: () => void;
}

const defaultConfig: AppConfig = {
  schemas: {},
  resources: {
    llms: {},
    vector_stores: {},
    genie_rooms: {},
    tables: {},
    volumes: {},
    functions: {},
    warehouses: {},
    databases: {},
    connections: {},
  },
  tools: {},
  guardrails: {},
  prompts: {},
  agents: {},
  app: {
    name: 'my_agent_app',
    log_level: 'INFO',
    registered_model: {
      name: 'my_agent_model',
    },
    agents: [],
  },
};

export const useConfigStore = create<ConfigState>((set) => ({
  config: defaultConfig,
  
  setConfig: (config) => set({ config }),
  
  updateConfig: (updates) =>
    set((state) => ({
      config: { ...state.config, ...updates },
    })),
  
  addSchema: (name, schema) =>
    set((state) => ({
      config: {
        ...state.config,
        schemas: {
          ...state.config.schemas,
          [name]: schema,
        },
      },
    })),
  
  updateSchema: (name, updates) =>
    set((state) => {
      const schemas = { ...state.config.schemas };
      if (schemas?.[name]) {
        schemas[name] = { ...schemas[name], ...updates };
      }
      return {
        config: {
          ...state.config,
          schemas,
        },
      };
    }),
  
  removeSchema: (name) =>
    set((state) => {
      const schemas = { ...state.config.schemas };
      delete schemas?.[name];
      return {
        config: {
          ...state.config,
          schemas,
        },
      };
    }),
  
  addLLM: (name, llm) =>
    set((state) => ({
      config: {
        ...state.config,
        resources: {
          ...state.config.resources,
          llms: {
            ...state.config.resources?.llms,
            [name]: llm,
          },
        },
      },
    })),
  
  updateLLM: (name, updates) =>
    set((state) => {
      const llms = { ...state.config.resources?.llms };
      if (llms?.[name]) {
        llms[name] = { ...llms[name], ...updates };
      }
      return {
        config: {
          ...state.config,
          resources: {
            ...state.config.resources,
            llms: llms || {},
          },
        },
      };
    }),
  
  removeLLM: (name) =>
    set((state) => {
      const llms = { ...state.config.resources?.llms };
      delete llms?.[name];
      return {
        config: {
          ...state.config,
          resources: {
            ...state.config.resources,
            llms: llms || {},
          },
        },
      };
    }),
  
  addTool: (tool) =>
    set((state) => ({
      config: {
        ...state.config,
        tools: {
          ...state.config.tools,
          [tool.name]: tool,
        },
      },
    })),
  
  updateTool: (name, updates) =>
    set((state) => {
      const tools = { ...state.config.tools };
      if (tools?.[name]) {
        tools[name] = { ...tools[name], ...updates };
      }
      return {
        config: {
          ...state.config,
          tools,
        },
      };
    }),
  
  removeTool: (name) =>
    set((state) => {
      const tools = { ...state.config.tools };
      delete tools?.[name];
      return {
        config: {
          ...state.config,
          tools,
        },
      };
    }),
  
  addGuardrail: (guardrail) =>
    set((state) => ({
      config: {
        ...state.config,
        guardrails: {
          ...state.config.guardrails,
          [guardrail.name]: guardrail,
        },
      },
    })),
  
  updateGuardrail: (name, updates) =>
    set((state) => {
      const guardrails = { ...state.config.guardrails };
      if (guardrails?.[name]) {
        guardrails[name] = { ...guardrails[name], ...updates };
      }
      return {
        config: {
          ...state.config,
          guardrails,
        },
      };
    }),
  
  removeGuardrail: (name) =>
    set((state) => {
      const guardrails = { ...state.config.guardrails };
      delete guardrails?.[name];
      return {
        config: {
          ...state.config,
          guardrails,
        },
      };
    }),
  
  addPrompt: (prompt) =>
    set((state) => ({
      config: {
        ...state.config,
        prompts: {
          ...state.config.prompts,
          [prompt.name]: prompt,
        },
      },
    })),
  
  updatePrompt: (name, updates) =>
    set((state) => {
      const prompts = { ...state.config.prompts };
      if (prompts?.[name]) {
        prompts[name] = { ...prompts[name], ...updates };
      }
      return {
        config: {
          ...state.config,
          prompts,
        },
      };
    }),
  
  removePrompt: (name) =>
    set((state) => {
      const prompts = { ...state.config.prompts };
      delete prompts?.[name];
      return {
        config: {
          ...state.config,
          prompts,
        },
      };
    }),
  
  addAgent: (agent) =>
    set((state) => {
      const agents = { ...state.config.agents, [agent.name]: agent };
      const appAgents = [...(state.config.app?.agents || []), agent];
      
      return {
        config: {
          ...state.config,
          agents,
          app: state.config.app
            ? {
                ...state.config.app,
                agents: appAgents,
              }
            : undefined,
        },
      };
    }),
  
  updateAgent: (name, updates) =>
    set((state) => {
      const agents = { ...state.config.agents };
      if (agents?.[name]) {
        agents[name] = { ...agents[name], ...updates };
        
        // Update in app.agents array too
        const appAgents = state.config.app?.agents?.map((a) =>
          a.name === name ? { ...a, ...updates } : a
        ) || [];
        
        return {
          config: {
            ...state.config,
            agents,
            app: state.config.app
              ? {
                  ...state.config.app,
                  agents: appAgents,
                }
              : undefined,
          },
        };
      }
      return { config: state.config };
    }),
  
  removeAgent: (name) =>
    set((state) => {
      const agents = { ...state.config.agents };
      delete agents?.[name];
      const appAgents = state.config.app?.agents?.filter((a) => a.name !== name) || [];
      
      return {
        config: {
          ...state.config,
          agents,
          app: state.config.app
            ? {
                ...state.config.app,
                agents: appAgents,
              }
            : undefined,
        },
      };
    }),
  
  updateApp: (updates) =>
    set((state) => ({
      config: {
        ...state.config,
        app: state.config.app
          ? {
              ...state.config.app,
              ...updates,
            }
          : undefined,
      },
    })),
  
  reset: () => set({ config: defaultConfig }),
}));

