import { ActiveSection } from '@/App';
import SchemasSection from '../sections/SchemasSection';
import LLMsSection from '../sections/LLMsSection';
import ToolsSection from '../sections/ToolsSection';
import GuardrailsSection from '../sections/GuardrailsSection';
import AgentsSection from '../sections/AgentsSection';
import OrchestrationSection from '../sections/OrchestrationSection';
import AppConfigSection from '../sections/AppConfigSection';

interface ConfigPanelProps {
  activeSection: ActiveSection;
}

export default function ConfigPanel({ activeSection }: ConfigPanelProps) {
  const renderSection = () => {
    switch (activeSection) {
      case 'schemas':
        return <SchemasSection />;
      case 'llms':
        return <LLMsSection />;
      case 'tools':
        return <ToolsSection />;
      case 'guardrails':
        return <GuardrailsSection />;
      case 'agents':
        return <AgentsSection />;
      case 'orchestration':
        return <OrchestrationSection />;
      case 'app':
        return <AppConfigSection />;
      default:
        return null;
    }
  };

  return (
    <div className="h-full overflow-y-auto bg-slate-900/50">
      <div className="p-6 animate-fade-in">
        {renderSection()}
      </div>
    </div>
  );
}

