/**
 * Modal for configuring Databricks connection settings.
 * 
 * Reference: https://apps-cookbook.dev/docs/streamlit/authentication/users_obo
 */
import { useState, useEffect } from 'react';
import { Zap, Server, AlertCircle } from 'lucide-react';
import Modal from './Modal';
import Input from './Input';
import Button from './Button';
import {
  getDatabricksConfig,
  setDatabricksConfig,
  clearDatabricksConfig,
} from '../../services/databricksNativeApi';

interface DatabricksConfigModalProps {
  isOpen: boolean;
  onClose: () => void;
  onConfigured: () => void;
  autoDetectedHost?: string | null;
}

export default function DatabricksConfigModal({
  isOpen,
  onClose,
  onConfigured,
  autoDetectedHost,
}: DatabricksConfigModalProps) {
  const [host, setHost] = useState('');
  const [token, setToken] = useState('');
  const [testing, setTesting] = useState(false);
  const [testResult, setTestResult] = useState<{ success: boolean; message: string } | null>(null);
  
  const currentConfig = getDatabricksConfig();

  // Load existing config on open
  useEffect(() => {
    if (isOpen) {
      const config = getDatabricksConfig();
      if (config) {
        setHost(config.host);
        setToken(config.token);
      } else if (autoDetectedHost) {
        setHost(autoDetectedHost);
      }
      setTestResult(null);
    }
  }, [isOpen, autoDetectedHost]);

  const handleTest = async () => {
    if (!host || !token) {
      setTestResult({ success: false, message: 'Please enter both host and token.' });
      return;
    }

    setTesting(true);
    setTestResult(null);

    // Temporarily set config to test
    setDatabricksConfig({ host, token, source: 'manual' });

    try {
      const url = `${host.replace(/\/$/, '')}/api/2.1/preview/scim/v2/Me`;
      const response = await fetch(url, {
        headers: {
          'Authorization': `Bearer ${token}`,
          'Content-Type': 'application/json',
        },
      });

      if (response.ok) {
        const data = await response.json();
        setTestResult({
          success: true,
          message: `Connected as ${data.displayName || data.userName}`,
        });
      } else {
        const errorData = await response.json().catch(() => ({}));
        setTestResult({
          success: false,
          message: errorData.message || `Connection failed: ${response.statusText}`,
        });
        clearDatabricksConfig();
      }
    } catch (err) {
      setTestResult({
        success: false,
        message: err instanceof Error ? err.message : 'Connection failed',
      });
      clearDatabricksConfig();
    } finally {
      setTesting(false);
    }
  };

  const handleSave = () => {
    if (!host || !token) return;
    setDatabricksConfig({ host, token, source: 'manual' });
    onConfigured();
    onClose();
  };

  const handleDisconnect = () => {
    clearDatabricksConfig();
    setHost(autoDetectedHost || '');
    setToken('');
    setTestResult(null);
    onConfigured();
    onClose();
  };

  const getSourceLabel = (source?: string) => {
    switch (source) {
      case 'header': return 'On-Behalf-Of User Auth';
      case 'env': return 'Environment Variables';
      case 'manual': return 'Manual Configuration';
      default: return 'Unknown';
    }
  };

  return (
    <Modal
      isOpen={isOpen}
      onClose={onClose}
      title="Databricks Connection"
      description="Configure your Databricks workspace connection."
    >
      <div className="space-y-4">
        {/* Current connection info */}
        {currentConfig?.isAutoDetected && (
          <div className="p-3 rounded-lg bg-green-500/10 border border-green-500/20">
            <div className="flex items-start space-x-2">
              {currentConfig.source === 'header' ? (
                <Zap className="w-5 h-5 text-green-400 mt-0.5 flex-shrink-0" />
              ) : (
                <Server className="w-5 h-5 text-green-400 mt-0.5 flex-shrink-0" />
              )}
              <div className="text-sm">
                <p className="text-green-300 font-medium">
                  Connected via {getSourceLabel(currentConfig.source)}
                </p>
                <p className="text-green-400/80 mt-1">
                  {currentConfig.source === 'header' 
                    ? 'Using X-Forwarded-Access-Token from Databricks App proxy.'
                    : 'Using DATABRICKS_HOST and DATABRICKS_TOKEN environment variables.'}
                </p>
              </div>
            </div>
          </div>
        )}

        {/* Auto-detected host info */}
        {autoDetectedHost && !currentConfig && (
          <div className="p-3 rounded-lg bg-blue-500/10 border border-blue-500/20">
            <div className="flex items-start space-x-2">
              <Zap className="w-5 h-5 text-blue-400 mt-0.5 flex-shrink-0" />
              <div className="text-sm">
                <p className="text-blue-300 font-medium">Workspace Detected</p>
                <p className="text-blue-400/80 mt-1">
                  Host auto-detected. Enter your Personal Access Token or set <code className="text-xs bg-blue-900/50 px-1 rounded">DATABRICKS_TOKEN</code> env var.
                </p>
              </div>
            </div>
          </div>
        )}

        <Input
          label="Workspace URL"
          placeholder="https://your-workspace.cloud.databricks.com"
          value={host}
          onChange={(e) => setHost(e.target.value)}
          hint={autoDetectedHost ? "Auto-detected from URL" : "Your Databricks workspace URL"}
          required
          disabled={!!autoDetectedHost && !currentConfig}
        />
        <Input
          label="Personal Access Token"
          type="password"
          placeholder="dapi..."
          value={token}
          onChange={(e) => setToken(e.target.value)}
          hint="Or set DATABRICKS_TOKEN environment variable"
          required
        />

        {/* Help text */}
        {!currentConfig?.isAutoDetected && (
          <div className="p-3 rounded-lg bg-slate-800/50 border border-slate-700">
            <div className="flex items-start space-x-2">
              <AlertCircle className="w-5 h-5 text-slate-400 mt-0.5 flex-shrink-0" />
              <div className="text-sm text-slate-400">
                <p className="font-medium text-slate-300">Authentication Options</p>
                <ul className="mt-2 space-y-1 list-disc list-inside text-xs">
                  <li>Enable <strong>on-behalf-of-user auth</strong> in your Databricks App</li>
                  <li>Set <code className="bg-slate-700 px-1 rounded">DATABRICKS_TOKEN</code> environment variable</li>
                  <li>Enter token manually below</li>
                </ul>
              </div>
            </div>
          </div>
        )}

        {testResult && (
          <div
            className={`p-3 rounded-lg text-sm ${
              testResult.success
                ? 'bg-green-500/20 text-green-400 border border-green-500/30'
                : 'bg-red-500/20 text-red-400 border border-red-500/30'
            }`}
          >
            {testResult.message}
          </div>
        )}

        <div className="flex justify-between pt-4">
          <Button
            variant="danger"
            type="button"
            onClick={handleDisconnect}
            disabled={!getDatabricksConfig()}
          >
            Disconnect
          </Button>
          <div className="flex space-x-3">
            <Button variant="secondary" type="button" onClick={handleTest} disabled={testing}>
              {testing ? 'Testing...' : 'Test Connection'}
            </Button>
            <Button
              type="button"
              onClick={handleSave}
              disabled={!testResult?.success}
            >
              Save
            </Button>
          </div>
        </div>

        <div className="pt-4 border-t border-slate-700">
          <p className="text-xs text-slate-500">
            Credentials are stored in browser localStorage and sent only to your Databricks workspace.
          </p>
        </div>
      </div>
    </Modal>
  );
}
